.dropzone {
    border: 0px;
    min-height: 0px;
    padding: 0px;
    margin: 5px 0px;
    background: none;
    border: none;
}

#dropzone-clickable {
    border: 2px dashed #cfcfcf;
    padding: 10px;
    text-align: center;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    opacity: 1;
    &:hover {
        border-color: #9b9b9b;
    }
}

#dropzone-previews {}

.dropzone a.dz-remove, .dropzone-previews a.dz-remove {
    background: none;
    border: 0px;
    color: #007ac2;
    position: relative;
    text-decoration: none;
    display: inline;
    text-align: left;
    margin-top: 26px;
    padding: 0px 0px 1px;
    cursor: pointer;
}

.dropzone a.dz-remove:hover, .dropzone-previews a.dz-remove:hover {
    color: #da6100;
}

.dropzone a.dz-remove:after, .dropzone-previews a.dz-remove:after {
    border-bottom: 1px dotted #007ac2;
    bottom: 2px;
    content: '';
    height: 0;
    left: 0;
    position: absolute;
    right: 0;
}

.dropzone a.dz-remove:hover::after, .dropzone-previews a.dz-remove:hover::after {
    border-bottom: 1px dotted #da6100;
    bottom: 2px;
    content: '';
    height: 0;
    left: 0;
    position: absolute;
    right: 0;
}

.feedback-form-left .dz-preview {}

.dropzone.dz-drag-hover {
    background: none;
}

.dz-drag-hover #dropzone-clickable {
    display: block;
    background-color: #d6e5f5;
    border-color: #fff;
}

